/* Обнуление */
*,
::before,
::after {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
}
a{
    text-decoration: none;
}
ul,
ol,
li {
    list-style: none;
}
img {
    vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: inherit;
    font-size: inherit;
}
html,
body {
    height: 100%;
    line-height: 1;
    font-size: 16px;
    color: #000;
    font-family: 'Heebo';
}
// ----------------HEADER----------------
.header-container {
    max-width: 1035px;
    padding: 0 15px;
    box-sizing: content-box;
    margin: 0 auto;
}
.header {
    .menu {
        justify-content: end;
        display: flex;
        padding: 26px 0;
    }
    }
.header {
    .menu {
        .menu-list {
            display: flex;
        }
    }
}
.header {
    .menu {
        .menu-list {
            .menu-item {
                margin-right: 33px;
            }
        }
    }
}
.header {
    .menu {
        .menu-list {
            .menu-item:last-child {
                margin-right: 0px;
            }
        }
    }
}
.header {
    .menu {
        .menu-list {
            .menu-item a {
                font-weight: 500;
                font-size: 20px;
                line-height: 29px;
                color: rgb(0, 0, 0);
                transition: all 0.3s ease 0s;
            }
        }
    }
}
.header {
    .menu {
        .menu-list {
            .menu-item:hover a {
              color: #FF6464;
            }
        }
    }
}

//MAIN
    .main {
        margin-top: 46px;
    }
    .container {
        max-width: 856px;
        margin: 0 auto;
        padding: 0 15px;
        box-sizing: content-box;
}
//----------------------FIRST-SECTION---------------------
.first-section {
    .author-block {
        display: flex;
        padding: 76px 0;
    }
}
.first-section {
    .author-block {
        .author-content {
                display: flex;
                flex-direction: column;
                margin-right: 120px;
        }
    }
}
.first-section {
    .author-block {
        .author-content {
            h1 {
                font-weight: 700;
                font-size: 44px;
                line-height: 60px;
                color: rgb(33, 36, 61);
                margin-top: 12px;
            }
        }
    }
}
.first-section {
    .author-block {
        .author-content {
            p {
                font-size: 16px;
