}
.first-section {
    .author-block {
        .author-content {
            .button {
                margin-top: 33px;
                font-weight: 500;
                font-size: 20px;
                line-height: 29px;
                color: rgb(255, 255, 255);
                min-height: 47px;
                max-width: 208px;
                display: flex;
                align-items: center;
                justify-content: center;
                background: rgb(255, 100, 100);
                border-radius: 2px;
                transition: all 0.4s ease 0s;
                border: 1px solid transparent;
            }
        }
    }
}

.first-section {
    .author-block {
        .author-content {
            .button:hover {
                color: rgb(255, 100, 100);
                background: rgb(255, 255, 255);
                border-color:  rgb(255, 100, 100);
